import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, LoadingController, Loading } from 'ionic-angular';
import { LocaldataProvider } from "../../providers/localdata/localdata";
import { LoginProvider } from "../../providers/login/login";
import { SignalrWindow } from "../../providers/constants/constants";
@IonicPage({
  name: 'login',
  segment: 'login'
})
@Component({
  selector: 'page-login',
  templateUrl: 'login.html'//,
  //providers: [LocaldataProvider,LocaldataProvider]
})
export class LoginPage implements OnInit, OnDestroy {
  showWeiXinLogin: boolean = true;
  disable: boolean = false;
  loading: Loading;

  ValidNumber: string;
  phoneNum: string;
  showType: string;
  mobile: string;
  unionid: string;
  nickname: string;
  avator: string;
  Countdown: 120;
  second: number = 0;
  firstSend: boolean = true;
  //输入的短信验证码
  verificationCode: string;
  canSend: boolean = true;
  intervalVal: any;
  cordova: any;
  constructor(public navCtrl: NavController, private lp: LocaldataProvider,
    private loadingCtrl: LoadingController, private loginp: LoginProvider,
    private window: SignalrWindow, private ref: ChangeDetectorRef) {

  }
  ngOnInit() {

    this.ref.markForCheck();
    var token = this.lp.getToken();
    if (token != null && token != "") {
      //token不为空就跳转到首页?如果token过期呢?
      //todo?
      this.navCtrl.push("tabs");
    }
  }
  ngOnDestroy() {
    this.ref.detach();
  }
  getVerificationCode() {
    //发送短信码
    this.canSend = false;
    this.second = 120;
    this.firstSend = false;
    this.intervalVal = setInterval(() => {
      if (this.second > 0) {
        this.second = this.second - 1;
      }
      if (this.second <= 0) {
        clearInterval(this.intervalVal);
        this.canSend = true;
      }
    }, 1000)
    //发送短信验证码
    this.loginp.LoginSendCode(this.mobile, 3).subscribe(data => {
      console.log(data);
      if (data.error_code == 1) {
        this.lp.alert("提示", "手机号没有注册，请点击‘注册’按钮进行注册");
      }
      this.ref.detectChanges();
    }, fail => {
      console.log(fail);
      this.lp.alert("提示", "服务器连接失败，请稍后再试。");
    });
    this.ref.detectChanges();
  }
  weixinlogin() {
    //微信登录
    let self = this;
    this.disable = true;
    //检测是否安装了微信
    Wechat.isInstalled(isInstall => {
      if (isInstall) {
        let scope = "snsapi_userinfo";
        let state = "_" + (+new Date());
        Wechat.auth(scope, state, success => {
          this.disable = true;
          let code = success.code;
          let state = success.state;
          let country = success.country;
          self.loadingInit();
          self.loading.present().then(() => {
            self.loginp.WeiXinLogin(code, state, country).subscribe(suc => {
              self.disable = false;
              self.loadingEnd(self);
              //  alert("登陆完成："+JSON.stringify(suc));
              if (suc.error_code == 0) {
                self.setToken(suc.data.access_token);
                // self.navCtrl.push("tabs");
              }
              else if (suc.error_code == 2 && suc.data && suc.data.unionid) {
                //未通过微信找到用户信息
                self.showWeiXinLogin = false;
                self.unionid = suc.data.unionid;
                self.nickname = suc.data.nickname;
                self.avator = suc.data.avator;
                self.lp.alert("提示", "请验证手机号码");
              }
              else {
                self.lp.alert("提示", "登录失败，请使用手机验证码登录或稍后再试");
              }
            }, fail => {
              self.disable = false;
              self.loadingEnd(self);
              self.lp.alert("提示", "登录失败，请使用手机验证码登录或稍后再试");
            });
          });

        }, fail => { self.disable = false; });
      } else {
        self.lp.alert("提示", "您没有安装微信APP，因此无法使用此方式登录。");
      }
      this.ref.detectChanges();

    }, fail => {
      this.disable = false;
    })
  }
  loadingInit() {
    this.loading = this.loadingCtrl.create({
      content: '请稍等...',
      dismissOnPageChange: true
    });
  }
  loadingEnd(obj) {
    obj.loading.dismissAll();
    obj.loading = null;
  }
  submitVerficationCode() {
    //短信码登录
    this.loadingInit();

    this.loading.present().then(() => {
      //验证通过 更新手机号码

      this.disable = true;

      this.loginp.Login(this.ValidNumber, 3, this.mobile, this.nickname, this.unionid, this.avator).subscribe(data => {
        this.loadingEnd(this);
        if (data.error_code != 0) {
          this.disable = false;
          this.lp.alert("错误", data.error_message);
        } else {
          console.log("登錄成功");
          //todo 路由 
          this.setToken(data.data.access_token);
          // this.navCtrl.push("tabs");
        }
      }, error => {

        this.disable = false;
        this.loadingEnd(this);
        console.log(error);
        this.lp.alert("提示", "服务器链接失败，请稍后再试。");
      });
      this.ref.detectChanges();
    });

  }

  setToken(token: string) {
    this.lp.setToken(token);
    var locaturl = this.window.nativeWindow.location.toString();
    locaturl = locaturl.substring(0, locaturl.lastIndexOf('#'));
    this.window.nativeWindow.location.replace(locaturl);
  }
  getUser(cb: any, obj: any) {
    obj.loginp.getMyInfo().subscribe(
      data => {
        cb && cb(data);
      },
      err => { console.log(err) });
  }


  //所有产品列表
  getFindProductAll(cb: any) {
    this.loginp.getFindProductAll().subscribe(
      data => {
        cb && cb(data);
      },
      err => { console.log(err) });
  }
}





